/*
 * List
 *****************************************************************************/

.ec-message {
    min-height: 20px;
    padding: 19px;
    margin-bottom: 20px;
    background-color: #f0f0f0;
    border: 1px solid #e3e3e3;
    border-radius: 4px;
    -webkit-box-shadow: inset 0 1px 1px rgba(0,0,0,.05);
    box-shadow: inset 0 1px 1px rgba(0,0,0,.05);
}
.ec-message p:last-child {
    margin-bottom: 0;
}
.ec-message__meta {
    margin: 0 0 6px 0;
    line-height: 32px;
}
.ec-message__meta .ec-message__author {
    float: left;
    font-weight: bold;
}
.ec-message__meta .ec-message__date {
    float: left;
}
.ec-message__meta .ec-message__votes {
    float: right;
}
.ec-message__date {
    font-size: 12px;
    color: #999;
    padding-left: 20px;
}

.ec-message__reply {
    background: #fff;
    padding: 10px;
    margin-left: 20px;
}
.ec-message__reply p:last-child {
    margin-bottom: 0;
}
/*
 * Votes
 *****************************************************************************/
.ec-message__votes {
    position: relative;
    padding: 5px 15px;
    background: #ffffff;
    border-radius: 5px 5px 0 0;
}
.ec-message__votes-item {
    float: left;
    display: block;
}
.ec-message__votes-item + .ec-message__votes-item {
    margin-left: 15px;
}
.ec-message__votes-button {
    display: block;
    height: 22px;
    line-height: 22px;
    padding-left: 20px;
    cursor: default;

    color: #8b8b8b;
    text-decoration: none;
}
.ec-message__votes-button:hover,
.ec-message__votes-button:active,
.ec-message__votes-button:focus {
    text-decoration: none;
}
.ec-message__votes-button:hover,
.ec-message__votes-button:active,
.ec-message__votes-button:focus {
    color: #8b8b8b;
}
.ec-message__votes-button.enabled {
    cursor: pointer;
}
.ec-message__votes-button.enabled:hover,
.ec-message__votes-button.enabled:active,
.ec-message__votes-button.enabled:focus {
    color: #337ab7;
}
.ec-message__votes-button.active {
    color: #337ab7;
}
.ec-message__votes-button-like {
    background: url(../../img/web/ec-votes.png) 0 0 no-repeat;
}
.ec-message__votes-button-dislike {
    background: url(../../img/web/ec-votes.png) 0 -22px no-repeat;
}
.ec-message__votes-button-like.enabled:hover {
    background-position: 0 -88px;
}
.ec-message__votes-button-dislike.enabled:hover {
    background-position: 0 -110px;
}
.ec-message__votes-button-like.active {
    background-position: 0 -88px;
}
.ec-message__votes-button-dislike.active {
    background-position: 0 -110px;
}
.ec-message__votes-bar {
    display: block;
    position: absolute;
    height: 2px;
    bottom: -2px;
    left: 0;
    right: 0;
    background: #dddddd;
}
.ec-message__votes-bar span {
    display: block;
    width: 0;
    height: 2px;
    background: #337ab7;
}
/*
 * Form
 *****************************************************************************/
.ec-error {
    display: none;
    color: #d9534f;
    font-size: $font-size-small;
    position: relative;
    top: -1rem;
}

.ec-antispam {
    display: none;
}

/*
 * Common, stars image
 *****************************************************************************/
.ec-rating-stars span,
.ec-stars,
.ec-stars span {
    background-image: url('data:image/png;base64,');
}
/*
 * Rating input
 *****************************************************************************/
// .ec-rating {

// }
.ec-rating-stars {
    float:left;
    margin-right: 20px;
}
.ec-rating-stars span {
    background-position: 0 -16px;
    background-repeat: no-repeat;
    width: 20px;
    height: 16px;
    float:left;
    display:block;
    margin: 0;
    cursor:pointer;
}
.ec-rating-stars span.active {
    background-position: 0 0;
}
.ec-rating-stars span.active2 {
    background-position: 0 0;
}
.ec-rating-stars span.active-disabled {
    background-position: 0 -16px;
}
.ec-rating-description {
    float:left;
    padding-top: 1px;
    color:#999;
}

/*
 * Stars
 *****************************************************************************/
.ec-stars {
    background-position: 0 -16px;
    background-repeat: repeat-x;
    margin: 0.2rem 0 0 !important;
    width: 96px;
    height: 16px;
    position: absolute;
    // bottom: 0;
    left: 50%;
	transform: translateX(-50%);
}
.ec-stars span{
    background-position: 0 0;
    background-repeat: repeat-x;
    display: block;
    height: 16px;
    width: 0;
}

.ec-stars span.rating-0{width: 0;}
.ec-stars span.rating-1{width: 16px;}
.ec-stars span.rating-2{width: 36px;}
.ec-stars span.rating-3{width: 56px;}
.ec-stars span.rating-4{width: 76px;}
.ec-stars span.rating-5{width: 96px;}

/*
 * Detailed Rating
 *****************************************************************************/
.ec-d-rating {
    width: 100%;
    max-width: 500px;
    background: #eee;
    padding: 20px;
    border-radius: 5px;
    border: 1px solid #e3e3e3;
    display: flex;
}
.ec-d-rating:after {
    content: "";
    clear: both;
    display: table;
}
.ec-d-rating__col-info {
    width: 130px;
    padding-top: 12px;
}
.ec-d-rating__col-lines {
    width: 250px;
    width: calc(100% - 130px);
}
.ec-d-rating__value {
    font-size: 50px;
    line-height: 1;
    color: #333333;
}
// .ec-d-rating__stars {

// }
.ec-d-rating__desc {
    margin-top: 12px;
    color: #8b8b8b;
}

// .ec-d-rating__lines {

// }
.ec-d-rating__line {
    position: relative;
    line-height: 1;
    color: #8b8b8b;
    margin: 10px 0;
}
.ec-d-rating__line:after {
    content: "";
    clear: both;
    display: table;
}
.ec-d-rating__line-rate {
    float: left;
    text-align: left;
    padding-left: 5px;
}
.ec-d-rating__line-progress {
    position: absolute;
    left: 30px;
    right: 60px;
    top: 50%;
    margin-top: -2px;
    background-color: #dddddd;
}
.ec-d-rating__line-progress > span {
    display: block;
    height: 4px;
    background-color: #337ab7;
}
.ec-d-rating__line-volume {
    float: right;
    width: 60px;
    text-align: right;
}

/*
 * Clearfix
 *****************************************************************************/
.ec-clearfix:before,
.ec-clearfix:after {
    content: " ";
    display: table;
}
.ec-clearfix:after {
    clear: both;
}
.ec-clearfix {
    *zoom: 1;
}