.nav-button {
  width: 3rem;
  height: 3rem;
  display: none;
  visibility: hidden;
  align-items: center;
  justify-content: center;
  position: fixed;
  top: 0;
  right: 0;
  cursor: pointer;
  z-index: 9998;
}
.nav-button-line {
  width: 50%;
  height: 0.15rem;
  background-color: #830800;
  position: absolute;
}
