  transform: translate(0, -290%);
  transform-origin: 50% 50%;
}
.nav-button-line__middle {
  transform-origin: 50% 50%;
}
.nav-button-line__bottom {
  transform: translate(0, 290%);
  transform-origin: 50% 50%;
}
.nav-mobile {
  width: calc(80% - (1rem * 4));
  height: calc(100vh - (3rem * 2));
  align-items: center;
  position: fixed;
  top: 0;
  right: 0;
  padding: 3rem 2rem;
  background-color: #fff;
  border-left: 0.6rem solid #830800;
  z-index: 9995;
  transform: translateX(100%);
}
.nav-mobile__mask {
