}
@media (min-width: 1680px) and (max-width: 2559px) {
  .nav-mobile {
    display: none;
    visibility: hidden;
  }
}
@media (min-width: 1440px) and (max-width: 1680px) {
  .nav-mobile {
    display: none;
    visibility: hidden;
  }
}
@media (min-width: 1024px) and (max-width: 1440px) {
  .nav-mobile {
    display: none;
    visibility: hidden;
  }
