.jGrowl {
  z-index: 99999;
  font-size: 12px;
  color: #fff;
  font-family: "Helvetica Neue", Helvetica, Arial, sans-serif;
  position: fixed;
}

.jGrowl.top-left {
  left: 0;
  top: 0;
}

.jGrowl.top-right {
  right: 0;
  top: 5rem;
}
@media (max-width: 1024px) {
  .jGrowl.top-right {
    right: 0;
    top: 0;
  }
}

.jGrowl.bottom-left {
  left: 0;
  bottom: 0;
}

.jGrowl.bottom-right {
  right: 0;
  bottom: 0;
}

.jGrowl.top-center {
  top: 50%;
  left: 32.5%;
  transform: translate(-50%, -50%);
}

.jGrowl-notification {
  width: 15rem;
  height: calc(5rem - 2rem);
  opacity: 0.9;
  zoom: 1;
  padding: 1rem;
  text-align: left;
  display: none;
}

.jGrowl-notification .ui-state-highlight,
.jGrowl-notification .ui-widget-content .ui-state-highlight,
.jGrowl-notification .ui-widget-header .ui-state-highlight {
  border: 1px solid #000;
  background: #000;
  color: #fff;
}

.jGrowl-notification .jGrowl-header {
  font-weight: 700;
  font-size: 0.85em;
}

.jGrowl-notification .jGrowl-close {
  background-color: transparent;
  color: inherit;
  border: none;
  z-index: 99;
  float: right;
  font-weight: 700;
  font-size: 1em;
  cursor: pointer;
}

.jGrowl-closer {
  width: 15rem;
  background-color: #000;
  opacity: 0.9;
  zoom: 1;
  padding: 0.5rem;
  display: none;
  padding-bottom: 4px;
  cursor: pointer;
  font-size: 0.9em;
  font-weight: 700;
  text-align: center;
}
@media (max-width: 570px) {
  .jGrowl-closer {
    width: calc(100% - 1rem);
  }
}

.jGrowl-closer .ui-state-highlight,
.jGrowl-closer .ui-widget-content .ui-state-highlight,
.jGrowl-closer .ui-widget-header .ui-state-highlight {
  border: 1px solid #000;
  background: #000;
  color: #fff;
}

@media print {
  .jGrowl {
    display: none;
  }
}
div.jGrowl > .ui-state-highlight {
  background: inherit;
  color: inherit;
  border: inherit;
}

.af-message-success {
  color: #000 !important;
  background-color: #fff !important;
}

.af-message-error {
  color: #fff;
  background-color: #000 !important;
}

.af-message-info {
  color: #000 !important;
