  text-align: center;
  opacity: 0.65;
  padding: 0 0 18px 10px;
  color: #FFF;
  font-style: normal;
  font-size: 28px;
  font-family: Arial, Baskerville, monospace;
}

.mfp-close:hover,
.mfp-close:focus {
  opacity: 1;
}

.mfp-close:active {
  top: 1px;
}

.mfp-close-btn-in .mfp-close {
  color: #333;
}

.mfp-image-holder .mfp-close,
.mfp-iframe-holder .mfp-close {
  color: #FFF;
  right: -6px;
  text-align: right;
  padding-right: 6px;
  width: 100%;
}

.mfp-counter {
  position: absolute;
  top: 0;
  right: 0;
  color: #CCC;
  font-size: 12px;
  line-height: 18px;
  white-space: nowrap;
}

.mfp-arrow {
  position: absolute;
  opacity: 0.65;
  margin: 0;
  top: 50%;
  margin-top: -55px;
  padding: 0;
  width: 90px;
  height: 110px;
  -webkit-tap-highlight-color: transparent;
}

.mfp-arrow:active {
  margin-top: -54px;
}

.mfp-arrow:hover,
.mfp-arrow:focus {
  opacity: 1;
}

.mfp-arrow:before,
.mfp-arrow:after {
  content: "";
  display: block;
  width: 0;
  height: 0;
  position: absolute;
  left: 0;
  top: 0;
  margin-top: 35px;
  margin-left: 35px;
  border: medium inset transparent;
}

.mfp-arrow:after {
  border-top-width: 13px;
  border-bottom-width: 13px;
  top: 8px;
}

.mfp-arrow:before {
  border-top-width: 21px;
  border-bottom-width: 21px;
  opacity: 0.7;
}

.mfp-arrow-left {
  left: 0;
}

.mfp-arrow-left:after {
  border-right: 17px solid #FFF;
  margin-left: 31px;
}

.mfp-arrow-left:before {
  margin-left: 25px;
  border-right: 27px solid #3F3F3F;
}

.mfp-arrow-right {
  right: 0;
}

.mfp-arrow-right:after {
  border-left: 17px solid #FFF;
  margin-left: 39px;
}

.mfp-arrow-right:before {
  border-left: 27px solid #3F3F3F;
}

.mfp-iframe-holder {
  padding-top: 40px;
  padding-bottom: 40px;
}

.mfp-iframe-holder .mfp-content {
  line-height: 0;
  width: 100%;
  max-width: 900px;
}

.mfp-iframe-holder .mfp-close {
  top: -40px;
}

.mfp-iframe-scaler {
  width: 100%;
  height: 0;
  overflow: hidden;
  padding-top: 56.25%;
}

.mfp-iframe-scaler iframe {
  position: absolute;
  display: block;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  box-shadow: 0 0 8px rgba(0, 0, 0, 0.6);
  background: #000;
}

/* Main image in popup */
img.mfp-img {
  width: auto;
  max-width: 100%;
  height: auto;
  display: block;
  line-height: 0;
  box-sizing: border-box;
  padding: 40px 0 40px;
  margin: 0 auto;
}

/* The shadow behind the image */
.mfp-figure {
  line-height: 0;
}

.mfp-figure:after {
  content: "";
  position: absolute;
  left: 0;
  top: 40px;
  bottom: 40px;
  display: block;
  right: 0;
  width: auto;
  height: auto;
  z-index: -1;
  box-shadow: 0 0 8px rgba(0, 0, 0, 0.6);
  background: #444;
}

.mfp-figure small {
  color: #BDBDBD;
  display: block;
  font-size: 12px;
  line-height: 14px;
}

.mfp-figure figure {
  margin: 0;
}

.mfp-bottom-bar {
  margin-top: -36px;
  position: absolute;
  top: 100%;
  left: 0;
  width: 100%;
  cursor: auto;
}

.mfp-title {
  text-align: left;
  line-height: 18px;
  color: #F3F3F3;
  word-wrap: break-word;
  padding-right: 36px;
}

.mfp-image-holder .mfp-content {
  max-width: 100%;
}

.mfp-gallery .mfp-image-holder .mfp-figure {
  cursor: pointer;
}

@media screen and (max-width: 800px) and (orientation: landscape), screen and (max-height: 300px) {
  /**
       * Remove all paddings around the image on small screen
       */
  .mfp-img-mobile .mfp-image-holder {
    padding-left: 0;
    padding-right: 0;
  }
  .mfp-img-mobile img.mfp-img {
    padding: 0;
  }
  .mfp-img-mobile .mfp-figure:after {
    top: 0;
    bottom: 0;
  }
  .mfp-img-mobile .mfp-figure small {
    display: inline;
    margin-left: 5px;
  }
  .mfp-img-mobile .mfp-bottom-bar {
    background: rgba(0, 0, 0, 0.6);
    bottom: 0;
    margin: 0;
    top: auto;
    padding: 3px 5px;
    position: fixed;
    box-sizing: border-box;
  }
  .mfp-img-mobile .mfp-bottom-bar:empty {
    padding: 0;
  }
  .mfp-img-mobile .mfp-counter {
    right: 5px;
    top: 3px;
  }
  .mfp-img-mobile .mfp-close {
    top: 0;
    right: 0;
    width: 35px;
    height: 35px;
    line-height: 35px;
    background: rgba(0, 0, 0, 0.6);
    position: fixed;
    text-align: center;
    padding: 0;
  }
}
@media all and (max-width: 900px) {
  .mfp-arrow {
    -webkit-transform: scale(0.75);
    transform: scale(0.75);
  }
  .mfp-arrow-left {
    -webkit-transform-origin: 0;
    transform-origin: 0;
  }
  .mfp-arrow-right {
    -webkit-transform-origin: 100%;
    transform-origin: 100%;
  }
  .mfp-container {
    padding-left: 6px;
    padding-right: 6px;
  }
}
/*=============================================
=                   Login                   =
=============================================*/
.login-block {
  width: 50%;
  height: calc(2rem * 2);
  display: block;
  float: left;
  position: absolute;
  right: 0;
  transform: translateY(-50%);
}
.login-block__top {
  width: 100%;
  height: 2rem;
  display: flex;
  flex-direction: row;
  align-items: center;
  position: relative;
  top: 0;
}
.login-block__bottom {
  width: 100%;
  height: 2rem;
  display: flex;
  flex-direction: row;
  justify-content: flex-end;
  position: relative;
}
.login-block__bottom li.login-block__part {
  display: flex;
  align-items: center;
  justify-content: flex-end;
  text-align: right;
  margin-right: calc(2rem / 2);
}
.login-block__bottom li.login-block__part a {
  font-size: 0.8rem;
  color: #fe9c58;
}
.login-block__bottom li.login-block__part i {
  color: #fe9c58;
  margin-right: calc(2rem / 4);
}
.login-block__bottom li.login-block__part:last-child {
  margin-right: 0;
}
.login-block__bottom li.login-block__part--3 {
  width: 33.3333333333%;
}
.login-block__bottom li.login-block__part--2 {
  width: 50%;
}
.login-block__bottom li.login-block__part:hover a, .login-block__bottom li.login-block__part:hover i {
  color: #830800;
}
.login-block__name {
  width: calc(100% - (2rem / 2) - (2rem * 3));
  height: 2rem;
  display: flex;
  align-items: center;
  justify-content: flex-end;
  padding-right: calc(2rem / 2);
}
.login-block__name > span {
  font-size: 1.2rem;
  font-family: "Oswald", sans-serif;
  color: #830800;
}
.login-block__form {
  width: 100%;
  height: 2rem;
  display: flex;
  align-items: center;
  justify-content: flex-end;
}
.login-block__form-input {
  width: calc(25rem - (2rem * 3) * 2 - (2rem / 4) * 3 - (0.07rem * 2));
  height: calc(2rem - 0.07rem);
  display: inline-block;
  border: 0.07rem solid #830800;
  border-bottom: none;
  padding: 0 calc(2rem / 4);
  margin-right: calc(2rem / 4);
  background-color: transparent;
  color: #830800;
  transition: all 0.3s;
}
.login-block__form-input:focus, .login-block__form-input:visited {
  color: #830800;
  background-color: #fff;
  border-color: #830800;
}
.login-block__form-input:focus::-webkit-input-placeholder {
  opacity: 0;
  transition: all 0.3s;
}
.login-block__form-input:focus::-moz-placeholder {
  opacity: 0;
  transition: all 0.3s;
}
.login-block__form-input:focus::-ms-input-placeholder {
  opacity: 0;
  transition: all 0.3s;
}
.login-block__form-input::-webkit-input-placeholder {
  /* WebKit browsers */
  color: rgba(131, 8, 0, 0.7);
}
.login-block__form-input::-moz-placeholder {
  /* Mozilla Firefox 4 to 18 */
  color: rgba(131, 8, 0, 0.7);
  opacity: 1;
}
.login-block__form-input::-moz-placeholder {
  /* Mozilla Firefox 19+ */
  color: rgba(131, 8, 0, 0.7);
  opacity: 1;
}
.login-block__form-input::-ms-input-placeholder {
  /* Internet Explorer 10+ */
  color: rgba(131, 8, 0, 0.7);
}
.login-block__button {
  width: calc(2rem * 3);
  height: 2rem;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #830800;
}
.login-block__button a {
  color: #feba8b;
}
.login-block__button:hover a {
  color: #fff;
}
.login-block__button--enter {
  outline: none;
  border: none;
  color: #feba8b;
  font-size: 1rem;
  cursor: pointer;
}
.login-block__button--enter:hover {
  color: #fff;
}
.login-block__button--single {
  width: initial;
  padding: 0 1rem;
}
@media (max-width: 570px) {
  .login-block {
    width: 100%;
    height: inherit;
    min-height: 2rem;
    position: relative;
    margin-top: calc(2rem / 2);
    transform: translateY(0);
  }
  .login-block__top {
    height: inherit;
    flex-direction: column;
    min-height: 2rem;
  }
  .login-block__bottom {
    height: calc(2rem * 1.5);
  }
  .login-block__name {
    width: calc(100% - (2rem / 2));
    height: calc(2rem * 2);
    justify-content: flex-start;
    padding-right: calc(2rem / 2);
  }
  .login-block__form {
    height: inherit;
    min-height: 2rem;
    flex-direction: column;
  }
  .login-block__form-input {
    width: calc(100% - (2rem / 4) * 2 - (0.07rem * 2));
    border-bottom: 0.07rem solid #830800;
    margin-right: 0;
    margin-bottom: calc(2rem / 4);
  }
  .login-block__button {
    width: 100%;
    height: calc(2rem * 2);
  }
}
.login-account {
  width: calc(100% - (2rem * 2));
  min-height: 3rem;
  display: flex;
  flex-direction: row;
  position: relative;
  padding: 2rem;
  margin-bottom: 2rem;
  background-color: #fff0e6;
}
.login-account--half {
  width: calc(50% - (2rem * 2));
  min-height: 10vh;
  display: block;
  float: left;
}
.login-account__part {
  width: 50%;
  height: 100%;
  display: block;
  float: left;
  position: relative;
  margin-bottom: 0 !important;
}
.login-account__part--full {
  width: 100%;
}
.login-account__part li {
  list-style: none;
  font-family: "Oswald", sans-serif;
  font-size: calc(1rem + 0.15vmax);
  color: #830800;
}
.login-account__part span {
  color: #fe9c58;
}
.login-account__part p {
  color: #830800;
}
.login-account__part p a {
  color: #fe9c58;
}
@media (max-width: 570px) {
  .login-account {
    width: calc(100% - (2rem * 1));
    padding: calc(2rem / 2);
    flex-direction: column;
  }
  .login-account__part {
    width: 100%;
  }
}

/*============  End of Login  =============*/
/*======================================
=            Permission 18+            =
======================================*/
.permission-cookie {
  width: calc(83.3333333333% - (8rem * 3));
  min-height: 5rem;
  display: flex;
  flex-direction: column;
  position: fixed;
  padding: 1rem calc(8rem * 2) 1rem calc(8rem / 2);
  right: 0;
  bottom: 0;
  background-color: #fff;
  box-shadow: 0 -0.1rem 0.1rem rgba(0, 0, 0, 0.2);
  z-index: 99999;
}
.permission-cookie__text p {
  font-size: 0.8rem;
}
.permission-cookie__text a {
  font-weight: 600;
}
.permission-cookie__text a:hover {
  color: #fed8be;
}
.permission-cookie__button {
  width: 8rem;
  height: 3rem;
  display: flex;
  align-items: center;
  justify-content: center;
  position: absolute;
  top: 2rem;
  right: calc(8rem / 2);
  background-color: #830800;
  color: #fff;
  cursor: pointer;
  transition: background 0.3s;
}
.permission-cookie__button:hover {
  background-color: #6a0600;
}
.permission-visible {
  display: block;
  visibility: visible;
}
.permission-hidden {
  display: none;
  visibility: hidden;
}
@media (max-width: 1024px) {
  .permission-cookie {
    width: calc(100% - 2rem);
    padding: 1rem;
  }
  .permission-cookie__text {
    height: 80%;
  }
  .permission-cookie__button {
    width: 100%;
    top: initial;
    right: initial;
    left: 0;
    position: relative;
  }
}

/*=====  End of Permission 18+  ======*/